@import url("https://fonts.googleapis.com/css2?family=Russo+One&family=Varela+Round&display=swap");
* {
      font-family: "Russo One", sans-serif;
      font-family: "Varela Round", sans-serif;
}
.wrapper {
      width: 365px;
      border-radius: 20px;
      box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.4),
            3px 3px 3px 5px rgba(0, 0, 0, 0.4);
      button {
            width: 90px;
            height: 90px;
            font-size: 30px;
            border-radius: 40px;
            color: #5f4f4f;
            border-width: 10px;
            border-color: #fff;

            &:hover {
                  background-color: rgb(2, 2, 2);
                  color: #fff;
                  transition: all 2s ease;
            }
      }
      input {
            text-align: right;
            align-items: center;
            width: 100%;
            outline: none;
      }
      input[type="text"] {
            font-family: inherit;
            line-height: inherit;
            font-size: 45px;
            color: #000;
            padding-right: 35px;
      }
}
